import { Injectable } from '@angular/core';
import { chain, find, reduce, some } from 'lodash';

import {
  IActionType,
  IBundleDescriptor,
  RX_DESIGNER,
  RX_DESIGNER_ELEMENT_SHAPE,
  RxActionTypeUtilsService,
  RxBundleCacheService,
  RxDesignerCacheService,
  RxGlobalCacheService
} from '@helix/platform/shared/api';
import { IDesignerPaletteNode, IDesignerPaletteTree } from '@helix/platform/shared/components';
import {
  IProcessElementDescriptor,
  RX_PROCESS_DEFINITION,
  RxProcessDefinitionService,
  RxProcessElementRegistryService
} from '@helix/platform/process/api';

@Injectable({
  providedIn: 'root'
})
export class RxProcessDesignerService {
  constructor(
    private rxActionTypeUtilsService: RxActionTypeUtilsService,
    private rxBundleCacheService: RxBundleCacheService,
    private rxDesignerCacheService: RxDesignerCacheService,
    private rxGlobalCacheService: RxGlobalCacheService,
    private rxProcessDefinitionService: RxProcessDefinitionService,
    private rxProcessElementRegistryService: RxProcessElementRegistryService
  ) {}

  buildPalette(actionTypes: IActionType[], bundleDescriptors: IBundleDescriptor[]): IDesignerPaletteTree[] {
    const registeredElementNodes = reduce<IProcessElementDescriptor, IDesignerPaletteNode[]>(
      this.rxProcessElementRegistryService.getAll(),
      (nodes, registeredElement) => {
        if (registeredElement.displayName) {
          nodes.push({
            group: registeredElement.group,
            label: registeredElement.displayName,
            paletteItem: registeredElement.paletteItem,
            value: {
              actionTypeName: this.rxProcessDefinitionService.getServerActionTypeName(registeredElement.type),
              resourceType: registeredElement.resourceType,
              type: registeredElement.type
            }
          });
        }

        return nodes;
      },
      []
    );

    const serverActionNodes = reduce<IActionType, IDesignerPaletteNode[]>(
      actionTypes,
      (nodes, actionType) => {
        const isActionRegistered = some(
          registeredElementNodes,
          (registeredElementNode) => registeredElementNode.value.actionTypeName === actionType.actionTypeName
        );

        if (!isActionRegistered) {
          nodes.push({
            group:
              this.rxActionTypeUtilsService.getActionTypeBundleFriendlyName(bundleDescriptors, actionType) ||
              RX_PROCESS_DEFINITION.standardProcessElementGroups.platformActions.name,
            label:
              actionType.displayName || this.rxActionTypeUtilsService.prettifyActionTypeName(actionType.actionTypeName),
            paletteItem: {
              border: RX_DESIGNER.paletteItemBorder.solid,
              icon: {
                path: actionType.isDeprecated
                  ? RX_DESIGNER_ELEMENT_SHAPE.bpmnIcons.info
                  : RX_DESIGNER_ELEMENT_SHAPE.bpmnIcons.gear,
                position: RX_DESIGNER.paletteIconPosition.top
              },
              label: RX_DESIGNER.paletteItemLabel.inner,
              shape: RX_DESIGNER.paletteItemShape.rectangle
            },
            value: {
              actionTypeName: actionType.actionTypeName,
              resourceType: RX_PROCESS_DEFINITION.processElementResourceTypes.processAction,
              type: this.rxProcessDefinitionService.getServerActionModelType(actionType.actionTypeName)
            }
          });
        }

        return nodes;
      },
      []
    );

    return chain([...registeredElementNodes, ...serverActionNodes])
      .sortBy('label')
      .reduce<IDesignerPaletteTree[]>((tree, element) => {
        const group = find(tree, { label: element.group });

        if (group) {
          group.children.push(element);
        } else {
          tree.push({
            label: element.group,
            children: [element]
          });
        }

        return tree;
      }, [])
      .sortBy('label')
      .sortBy((node) => {
        const group = find(RX_PROCESS_DEFINITION.processElementGroups, { name: node.label });

        return group?.priority ?? RX_PROCESS_DEFINITION.standardProcessElementGroups.default.priority;
      })
      .value();
  }
}
