import {
  Component,
  EventEmitter,
  Injector,
  Input,
  OnChanges,
  OnDestroy,
  OnInit,
  Output,
  SimpleChanges,
  ViewChild
} from '@angular/core';
import { CdkDragDrop } from '@angular/cdk/drag-drop/drag-events';
import { TranslateService } from '@ngx-translate/core';
import { AdaptSidebarComponent } from '@bmc-ux/adapt-angular';
import { BehaviorSubject, combineLatest, forkJoin, merge, Observable, of, ReplaySubject, Subject } from 'rxjs';
import {
  debounceTime,
  distinctUntilChanged,
  map,
  shareReplay,
  startWith,
  switchMap,
  take,
  takeUntil,
  tap,
  withLatestFrom
} from 'rxjs/operators';
import { find, forEach, isEmpty, isEqual, reject, some } from 'lodash';

import { IValidationIssue, IValidationIssueSection, ValidationIssueType } from '@helix/platform/ui-kit';
import { RxGuidService, RxIdService } from '@helix/platform/utils';
import {
  IDesignerConfiguration,
  IPlainObject,
  RxBundleCacheService,
  RxCommandManagerService,
  RxDesignerCacheService,
  RxGlobalCacheService,
  RxOverlayService
} from '@helix/platform/shared/api';
import {
  IProcessDefinition,
  RxProcessDataDictionaryService,
  RxProcessDefinitionService,
  RxProcessElementRegistryService,
  RxProcessElementSearchService
} from '@helix/platform/process/api';
import { IProcessDefinitionModel, RxProcessService } from '@helix/platform/process/elements';

import { RxProcessDesignerService } from './process-designer.service';
import { RxProcessDesignerGraph } from './process-designer-graph.class';

@Component({
  selector: 'rx-process-designer',
  templateUrl: './process-designer.component.html',
  styleUrls: ['./process-designer.component.scss']
})
export class ProcessDesignerComponent implements OnInit, OnChanges, OnDestroy {
  @Input()
  configuration: IDesignerConfiguration;

  @Output()
  closeDesigner: EventEmitter<void> = new EventEmitter<void>();

  @Output()
  definitionErrorLoading: EventEmitter<void> = new EventEmitter();

  @Output()
  definitionSaved: EventEmitter<string> = new EventEmitter();

  @ViewChild('inspectorSidebar', { static: false })
  inspectorSidebar: AdaptSidebarComponent;

  // TODO-VS: update types
  droppedElement: CdkDragDrop<any>;

  private bundleIdSubject = new BehaviorSubject<string>(null);
  private definitionModelChangeSubject = new Subject<IProcessDefinitionModel>();
  private selectedElementModelChangeSubject = new Subject<any>();
  private definitionNameSubject = new BehaviorSubject<string>(null);
  private destroyed$ = new ReplaySubject<boolean>(1);
  private focusDefinitionInspectorSubject = new BehaviorSubject<{ editorName: string; data: IPlainObject }>(null);
  private focusSelectedElementInspectorSubject = new BehaviorSubject<{ editorName: string; data: IPlainObject }>(null);

  private graph = new RxProcessDesignerGraph({
    injector: this.injector,
    options: {
      processId: this.rxGuidService.generate()
    }
  });

  private graphChangeSubject = new Subject<RxProcessDesignerGraph>();
  private graphChange$ = this.graphChangeSubject.pipe(shareReplay(1));
  private isDesignModeSubject = new BehaviorSubject<boolean>(true);
  private selectedElementGuidSubject = new BehaviorSubject<string>(null);
  private selectedElementGuid$ = this.selectedElementGuidSubject.pipe(distinctUntilChanged(isEqual), shareReplay(1));

  private paletteElementsTree$ = forkJoin([
    this.rxBundleCacheService.getActionTypes(),
    this.rxGlobalCacheService.getBundleDescriptors(),
    this.rxGlobalCacheService.getFunctionDescriptors()
  ]).pipe(
    tap(([actionTypes, bundleDescriptors, functionDescriptors]) => {
      this.rxDesignerCacheService.setActionTypes(actionTypes);
      this.rxDesignerCacheService.setFunctionDescriptors(functionDescriptors);
    }),
    map(([actionTypes, bundleDescriptors]) =>
      this.rxProcessDesignerService.buildPalette(actionTypes, bundleDescriptors)
    ),
    shareReplay(1)
  );

  private definition$ = combineLatest([this.definitionNameSubject, this.paletteElementsTree$]).pipe(
    switchMap(([definitionName]) =>
      definitionName ? this.rxProcessDefinitionService.get(definitionName) : this.rxProcessDefinitionService.getNew()
    ),
    shareReplay(1)
  );

  private isReadOnly$ = combineLatest([
    this.definition$,
    this.bundleIdSubject.pipe(switchMap((bundleId) => this.rxOverlayService.areNewDefinitionsAllowed(bundleId)))
  ]).pipe(
    map(
      ([definition, areNewDefinitionsAllowed]) =>
        !areNewDefinitionsAllowed || !this.rxOverlayService.isCustomizationEnabled('allowOverlay', definition)
    ),
    distinctUntilChanged(),
    shareReplay(1)
  );

  private definitionModelFromDefinition$ = this.definition$.pipe(
    map((definition: IProcessDefinition) =>
      this.rxProcessService.getModelFromDefinition(definition, this.configuration.bundleId)
    )
  );

  private graph$ = combineLatest([this.definition$, this.definitionModelFromDefinition$, this.isReadOnly$]).pipe(
    tap(([definition, definitionModelFromDefinition, isReadOnly]) => {
      if (!isReadOnly) {
        this.graph.on('add', (element, elements, options) => {
          this.graphChangeSubject.next(this.graph);
        });

        this.graph.on('change', (element, elementModel, changedProperty) => {
          if (!element.changed.position) {
            this.graphChangeSubject.next(this.graph);
          }
        });
      }

      this.graph.loadGraphFromDefinition(definition);
      this.graph.addDefaultElements(definitionModelFromDefinition);
    }),
    map(() => this.graph),
    shareReplay(1)
  );

  private definitionModelFromGraph$ = this.graphChange$.pipe(
    map((graph) => graph.getCell(graph.get('processId')).prop('definitionModel')),
    distinctUntilChanged(isEqual)
  );

  private definitionModel$ = merge(this.definitionModelFromDefinition$, this.definitionModelFromGraph$).pipe(
    shareReplay(1)
  );

  private definitionInspectorConfig$ = this.definitionModel$.pipe(
    map((definitionModel) => this.rxProcessService.getInspectorConfig(definitionModel))
  );

  private selectedElementModelFromDefinition$ = combineLatest([this.definition$, this.selectedElementGuid$]).pipe(
    map(([definition, selectedElementGuid]) => {
      let selectedElementModel = null;

      if (selectedElementGuid) {
        const flowElement = this.rxProcessElementSearchService.find(definition, {
          guid: selectedElementGuid
        });

        if (flowElement) {
          const selectedElement = find(JSON.parse(definition.layout).cells, {
            id: this.rxIdService.getBase(selectedElementGuid)
          });

          const elementService = this.rxProcessElementRegistryService.get(selectedElement.type).elementService;

          selectedElementModel = elementService.getModelFromDefinition(flowElement);
        }
      }

      return selectedElementModel;
    })
  );

  private selectedElementModelFromGraph$ = combineLatest([this.graphChange$, this.selectedElementGuid$]).pipe(
    map(([graph, selectedElementGuid]) => {
      let selectedElementModel = null;

      if (selectedElementGuid) {
        const selectedElementCell = graph.getCell(this.rxIdService.getBase(selectedElementGuid));

        selectedElementModel = selectedElementCell.prop('elementModel');
      }

      return selectedElementModel;
    })
  );

  private selectedElementModel$ = merge(
    this.selectedElementModelFromDefinition$,
    this.selectedElementModelFromGraph$
  ).pipe(
    tap((selectedElementModel) => {
      if (this.inspectorSidebar?.isPanelOpened) {
        if (isEmpty(selectedElementModel)) {
          this.openInspectorSidebarPanel(0);
        } else {
          this.openInspectorSidebarPanel(1);
        }
      }
    }),
    shareReplay(1)
  );

  private selectedElementInspectorConfig$ = combineLatest([this.selectedElementModel$, this.definitionModel$]).pipe(
    map(([selectedElementModel, definitionModel]) => {
      let selectedElementInspectorConfig = null;

      if (selectedElementModel) {
        const elementService = this.rxProcessElementRegistryService.get(selectedElementModel.type).elementService;

        selectedElementInspectorConfig = elementService.getInspectorConfig(selectedElementModel, {
          processDefinitionModel: definitionModel
        });
      }

      return selectedElementInspectorConfig;
    })
  );

  private definitionForCodeViewer$ = this.isDesignModeSubject.pipe(
    switchMap((isDesignMode) => {
      return isDesignMode
        ? of(null)
        : this.graph$.pipe(
            take(1),
            map((graph) => graph.getDefinitionFromGraph())
          );
    })
  );

  private bundleFriendlyName$ = this.bundleIdSubject.pipe(
    switchMap((bundleId: string) => this.rxGlobalCacheService.getBundleFriendlyName(bundleId))
  );

  // TODO-VS: update canvas configuration (interactive property)
  private canvasConfiguration$ = this.isReadOnly$.pipe(
    map((isReadOnly) => {
      return {
        elementRegistry: this.rxProcessElementRegistryService,
        enableMultiSelection: true,
        interactive: !isReadOnly,
        isReadOnly,
        showToolbar: true
      };
    })
  );

  private breadcrumbItems$ = combineLatest([this.definitionModel$, this.selectedElementModel$]).pipe(
    map(([definitionModel, selectedElementModel]) => {
      const breadcrumbItems = [
        {
          label:
            definitionModel.name ||
            `<${this.translateService.instant('com.bmc.arsys.rx.client.process-designer.new-process.label')}>`,
          data: {}
        }
      ];

      if (selectedElementModel) {
        breadcrumbItems.push({
          label: selectedElementModel.label,
          data: {}
        });
      }

      return breadcrumbItems;
    })
  );

  private elementValidationIssueSections$: Observable<IValidationIssueSection[]> = this.graphChange$.pipe(
    switchMap((graph) => {
      const cells = reject(graph.getCells(), { id: graph.get('processId') });

      return forkJoin(
        cells.map((cell) => {
          const elementModel = cell.prop('elementModel');
          const cellType = cell.prop('type');

          const elementRegistry = this.rxProcessElementRegistryService.get(cellType);

          return elementRegistry.elementService.validate(elementModel, reject(cells, { id: elementModel.id })).pipe(
            map((elementValidationIssues) => ({
              title: cell.prop('elementModel').label || this.translateService.instant(elementRegistry.displayName),
              issues: elementValidationIssues
            }))
          );
        })
      );
    })
  );

  private processValidationIssueSection$: Observable<IValidationIssueSection> = this.graphChange$.pipe(
    switchMap((graph) => {
      const processCell = graph.getCell(graph.get('processId'));

      return this.rxProcessService.validate(
        processCell.prop('definitionModel'),
        reject(graph.getCells(), { id: processCell.id })
      );
    }),
    map((processValidationIssues) => ({
      title: this.translateService.instant('com.bmc.arsys.rx.client.process-definition.label'),
      issues: processValidationIssues
    }))
  );

  private validationIssueSections$ = combineLatest([
    this.processValidationIssueSection$,
    this.elementValidationIssueSections$
  ]).pipe(
    map(([processValidationIssueSection, elementValidationIssueSections]) => {
      const validationIssueSections: IValidationIssueSection[] = [];

      if (!isEmpty(processValidationIssueSection.issues)) {
        validationIssueSections.push(processValidationIssueSection);
      }

      elementValidationIssueSections.forEach((elementValidationIssueSection) => {
        if (!isEmpty(elementValidationIssueSection.issues)) {
          validationIssueSections.push(elementValidationIssueSection);
        }
      });

      return validationIssueSections;
    }),
    shareReplay(1)
  );

  private hasValidationErrors$ = this.validationIssueSections$.pipe(
    map((sections) => some(sections, (section) => find(section.issues, { type: ValidationIssueType.Error }))),
    distinctUntilChanged(),
    shareReplay(1)
  );

  // TODO-VS: update the logic to rely on command manager
  private isSaveButtonDisabled$ = combineLatest([this.hasValidationErrors$, this.isReadOnly$]).pipe(
    map(([hasValidationErrors, isReadOnly]) => hasValidationErrors || isReadOnly),
    startWith(true)
  );

  selectedElementInspectorDesignerItemModel$ = combineLatest([this.selectedElementModel$, this.graph$]).pipe(
    map(([selectedElementModel, graph]) => ({
      elementModel: selectedElementModel,
      graph
    }))
  );

  focusDefinitionInspector$ = this.focusDefinitionInspectorSubject.asObservable();
  focusSelectedElementInspector$ = this.focusSelectedElementInspectorSubject.asObservable();

  vm$ = combineLatest([
    this.breadcrumbItems$,
    this.bundleFriendlyName$,
    this.canvasConfiguration$,
    this.definitionForCodeViewer$,
    this.definitionInspectorConfig$,
    this.definitionModel$,
    this.graph$,
    this.hasValidationErrors$,
    this.isReadOnly$,
    this.isSaveButtonDisabled$,
    this.paletteElementsTree$,
    this.selectedElementInspectorConfig$,
    this.selectedElementModel$,
    this.validationIssueSections$
  ]).pipe(
    map(
      ([
        breadcrumbItems,
        bundleFriendlyName,
        canvasConfiguration,
        definitionForCodeViewer,
        definitionInspectorConfig,
        definitionModel,
        graph,
        hasValidationErrors,
        isReadOnly,
        isSaveButtonDisabled,
        paletteElementsTree,
        selectedElementInspectorConfig,
        selectedElementModel,
        validationIssueSections
      ]) => ({
        breadcrumbItems,
        bundleFriendlyName,
        canvasConfiguration,
        definitionForCodeViewer,
        definitionInspectorConfig,
        definitionModel,
        graph,
        hasValidationErrors,
        isReadOnly,
        isSaveButtonDisabled,
        paletteElementsTree,
        selectedElementInspectorConfig,
        selectedElementModel,
        validationIssueSections
      })
    )
  );

  constructor(
    private readonly injector: Injector,
    private readonly rxBundleCacheService: RxBundleCacheService,
    private readonly rxCommandManagerService: RxCommandManagerService,
    private readonly rxDesignerCacheService: RxDesignerCacheService,
    private readonly rxGlobalCacheService: RxGlobalCacheService,
    private readonly rxGuidService: RxGuidService,
    private readonly rxIdService: RxIdService,
    private readonly rxOverlayService: RxOverlayService,
    private readonly rxProcessDataDictionaryService: RxProcessDataDictionaryService,
    private readonly rxProcessDefinitionService: RxProcessDefinitionService,
    private readonly rxProcessDesignerService: RxProcessDesignerService,
    private readonly rxProcessElementRegistryService: RxProcessElementRegistryService,
    private readonly rxProcessElementSearchService: RxProcessElementSearchService,
    private readonly rxProcessService: RxProcessService,
    private readonly translateService: TranslateService
  ) {}

  ngOnInit(): void {
    // TODO-VS: update logic not to debounce model change here
    this.definitionModelChangeSubject
      .pipe(debounceTime(300), withLatestFrom(this.graph$), takeUntil(this.destroyed$))
      .subscribe(([definitionModel, graph]) => {
        const processCell = graph.getCell(graph.get('processId'));
        const commandManager = this.rxCommandManagerService.get();

        commandManager.initBatchCommand();

        forEach(definitionModel, (propertyValue, propertyName) => {
          processCell.prop(`definitionModel/${propertyName}`, propertyValue);
        });

        commandManager.storeBatchCommand();
      });

    // TODO-VS: update logic not to debounce model change here
    this.selectedElementModelChangeSubject
      .pipe(debounceTime(300), withLatestFrom(this.graph$), takeUntil(this.destroyed$))
      .subscribe(([selectedElementModel, graph]) => {
        const selectedElementCell = graph.getCell(this.rxIdService.getBase(selectedElementModel.guid));
        const commandManager = this.rxCommandManagerService.get();

        commandManager.initBatchCommand();

        forEach(selectedElementModel, (propertyValue, propertyName) => {
          selectedElementCell.prop(`elementModel/${propertyName}`, propertyValue);
        });

        commandManager.storeBatchCommand();
      });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.configuration.currentValue.definitionName) {
      this.graph.reset();
      this.rxProcessDataDictionaryService.clear();

      this.isDesignModeSubject.next(true);
      this.definitionNameSubject.next(changes.configuration.currentValue.definitionName);
    }

    if (changes.configuration.currentValue.bundleId) {
      this.bundleIdSubject.next(changes.configuration.currentValue.bundleId);
    }
  }

  ngOnDestroy(): void {
    this.rxProcessDataDictionaryService.clear();

    this.bundleIdSubject.complete();
    this.definitionModelChangeSubject.complete();
    this.definitionNameSubject.complete();
    this.focusDefinitionInspectorSubject.complete();
    this.focusSelectedElementInspectorSubject.complete();
    this.graphChangeSubject.complete();
    this.isDesignModeSubject.complete();
    this.selectedElementGuidSubject.complete();
    this.destroyed$.next(true);
    this.destroyed$.complete();
  }

  canDeactivate(): boolean {
    return true;
  }

  onBreadcrumbSelected(guid: string): void {
    if (guid) {
      this.openInspectorSidebarPanel(1);
    } else {
      this.openInspectorSidebarPanel(0);
    }
  }

  onCanvasElementSelected(guid: string): void {
    if (!guid) {
      this.openInspectorSidebarPanel(0);
    }

    this.selectedElementGuidSubject.next(guid);
  }

  onCorrectIssue(validationIssue: IValidationIssue): void {
    this.openInspectorSidebarPanel(validationIssue.data.inspectorTabIndex);

    if (validationIssue.data.inspectorTabIndex === 0) {
      setTimeout(
        () =>
          this.focusDefinitionInspectorSubject.next({
            editorName: validationIssue.data.propertyName,
            data: validationIssue.data
          }),
        10
      );
    } else if (validationIssue.data.inspectorTabIndex === 1) {
      this.selectedElementGuidSubject.next(validationIssue.data.guid);

      setTimeout(
        () =>
          this.focusSelectedElementInspectorSubject.next({
            editorName: validationIssue.data.propertyName,
            data: validationIssue.data
          }),
        10
      );
    }
  }

  onDefinitionInspectorInitialized(): void {
    this.focusDefinitionInspectorSubject.next({
      editorName: 'name',
      data: {}
    });
  }

  onDefinitionModelChange(definitionModel: IProcessDefinitionModel): void {
    this.definitionModelChangeSubject.next(definitionModel);
  }

  // TODO-VS: update types
  onPaletteElementDropped(dropData: CdkDragDrop<any>) {
    this.droppedElement = dropData;
  }

  onSelectedElementModelChange(selectedElementModel: any): void {
    this.selectedElementModelChangeSubject.next(selectedElementModel);
  }

  saveDefinition(): void {
    this.graph$
      .pipe(
        take(1),
        map((graph) => graph.getDefinitionFromGraph())
      )
      .subscribe();
  }

  toggleDesignMode(): void {
    this.isDesignModeSubject.next(!this.isDesignModeSubject.value);
  }

  private openInspectorSidebarPanel(id: number): void {
    this.inspectorSidebar.openPanel(id);
  }
}
